<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View, Search, CaretBottom, Filter } from '@element-plus/icons-vue'
export default {
  name: "ViewDrivers",
  components: { Search, CaretBottom, Filter },
  data(){
    return {
      statusMap: {
        ONGOING: { displayName: "Ongoing", color: "#f9c700" },
        COMPLETED: { displayName: "Completed", color: "#016a4e" },
      },
      multiSelectArray: [],
      showAdvanceFilters: false,
      searchColumns: [
        { name: "Driver Name", value: "RIDE_NO"},
        { name: "E mail", value: "USER_INFO.NAME"},
        { name: "Phone no", value: "USER_INFO.PH_NO"},
        { name: "Fleet", value: "DRIVER_INFO.NAME"},
        { name: "Last Seen", value: "DRIVER_INFO.PH_NO"},
        { name: "Created Date", value: "CREATED_DATE"}
      ],
      selectedSearch: "DRIVER_NAME",
      searchVal: "",
      p_perPage: 25,
      p_pageNo: 1,
      filterCols: {

      }
    }
  },
  computed: {
    filterTableData() {
      return this.tableData.filter(
          (data) =>
              !this.searchVal ||
              data[this.selectedSearch].toLowerCase().includes(this.searchVal.toLowerCase())
      )
    },
    p_total(){
      return this.tableData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    },
    tableData(){
      return [
        {
          STATUS: "ONGOING",
          RIDE_NO: "1221223",
          USER_INFO: {
            NAME: "Guest",
            PH_NO: "+91 6391360525"
          },
          DRIVER_INFO: {
            NAME: "Jaswanth SP",
            ID: "11223"
          },
          VEHICLE_INFO: "Auto",
          FARE_INFO: "150",
          DISTANCE: "2",
          PICKUP_TIME: "2016-05-01 1:12 am"
        },
        {
          STATUS: "COMPLETED",
          RIDE_NO: "1221222",
          USER_INFO: {
            NAME: "Guest",
            PH_NO: "+91 6391360525"
          },
          DRIVER_INFO: {
            NAME: "Jazz",
            ID: "+91 6391360525"
          },
          VEHICLE_INFO: "Car",
          FARE_INFO: "130",
          DISTANCE: "1.5",
          PICKUP_TIME: "2016-05-01 1:12 pm"
        }
      ]
    }
  },
  methods: {
    deleteFunc(){
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
        });
      }
      else{
        ElMessage({
          type: 'info',
          message: 'Please select rows to delete',
        })
      }
    },
    exportFunc(){
      ElMessage({
        type: 'success',
        message: 'Exported as CSV',
      })
    },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
      this.multiSelectArray = selected;
    },
    filterHandler(value, row, column){
      const property = column['property'];
      return row[property] === value;
    },
    filterChange(filters){
      console.log(filters);
    }
  }
}
